<template>
    <div class="ShoppingThinFlm">
      <!--      如果没有的时候-->
      <div class="falseShow" v-if="shows">
        <img src="../../../assets/img/common/falseShow.png" alt="">
        <p>{{falseShowText}}</p>
        <div  class="public-btn mt60 mr30 ml30">去购物车结算</div>
      </div>
      <div v-else>
        <div class="orderForm" v-for="(data,index) in OrderContent">
          <div class="title" @click="PcbParticulars">
            <p @click.stop="data.Noselect==true?SelectHe(data,index):''"><i class="icon_show" :class="data.Noselect==true?data.select==true?'bg_icon':'bg_icons':'bg_gray'"></i><span>{{data.name}}</span></p>
            <p><i class="iconfont">&#xe67c;</i></p>
          </div>
          <div class="bottom">
            <p class="left"><img src="../../../assets/img/common/succeed_f90.png" alt=""></p>
            <div class="right">
              <div class="file"><p><span>{{data.daxiao}}</span><span>{{data.jiceng}}</span></p><p class="cl_f90">￥{{data.jiage}}</p></div>
              <div class="file"><p><span>交期 ：</span><span>{{data.jiaoqi}}</span></p><p class="border">x {{data.jige}}</p></div>
              <div><span>上传文件：</span><span>{{data.wenjian}}</span></div>
              <div><span>添加时间：</span><span>2020-08-21 18:32:43</span></div>
            </div>
          </div>
        </div>
      </div>

      <div class="fixationBottom">
        <p @click="CheckAll(1)"  v-if="callMethodCut==true">
          <i class="icon_show" :class="choosable==true?CheckAllShow==true?'bg_icon':'bg_icons':'bg_icon'"></i>全选
        </p>
        <p @click="CheckAll(2)" v-else>
          <i class="icon_show" :class="choosable==true?DeleteAllShow==true?'bg_icon':'bg_icons':'bg_icon'"></i>全选
        </p>
        <div class="right">
          <div  v-if="callMethodCut==true">
            合计:<span class="cl_f90">${{totalPrice}}</span><p class="buttom" :class="choosable==true?'bg_f90':'bg_FFC266'" @click="choosable==true?PayMoney():''">付款{{CheckAllHome.length==0?'':'('+CheckAllHome.length+')'}}</p>
          </div>

          <p class="AddRemove" @click="choosable==true?AddRemove():''" v-else>删除</p>
        </div>
        <!--        <div class="right"></div>-->
      </div>
    </div>

</template>

<script>
    import Vue from "vue"

    export default {
        name: "ShoppingThinFlm",
        components: {},
      data() {
        return {
          //判断是否有值
          shows:false,
          Alldata:["11"],
          falseShowText:"暂无订单",
          OrderContent:[
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:true,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:true,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:false,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:false,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:false,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:false,},
            {name:"订单编号：#W2W-1620261",daxiao:"20cm*20cm",jiceng:"1层",jiaoqi:"48小时",wenjian:"XLSG391HB_LBDriverV1.2打...", time:"48小时", jiage:"361.00",jige:"1",select:true,Noselect:false,},
          ],
          //全选的判断
          CheckAllShow:true,
          CheckAllHome:[],
          //全选总价
          totalPrice:"",
          //找一个值来做付款按钮的颜色选定
          choosable:false,
          //兄弟组件调用的切换删除
          callMethodCut:true,
          //删除的全选判断
          DeleteAllShow:true,
          DeleteAllHome:[],
        }
      },
      methods: {
        //付款按钮
        PayMoney(){},
        //全选按钮
        CheckAll(i){
          if(i==1){
            this.CheckAllShow=!this.CheckAllShow;
            this.CheckFun(this.CheckAllShow,i);
          }else{
            this.DeleteAllShow=!this.DeleteAllShow;
            this.CheckFun(this.DeleteAllShow,i);
          }
        },
        //调用方法
        CheckFun(show,index){
          switch (index) {
            case 1:
              this.CheckFun1(show);
              break;
            case 2:
              this.CheckFun2(show);
              break;
          }
        },
        CheckFun1(show){
          if(show==false){
            this.OrderContent.forEach((item,index)=>{
              if(item.Noselect==true){
                item.select=false;
                console.log(item.jiage,index);
                this.totalPrice=Number(item.jiage);
                this.CheckAllHome.push(item);
              }
            });
          }else{
            this.OrderContent.forEach((item,index)=>{
              if(item.Noselect==true){
                item.select=true
              }
            });
            this.CheckAllHome=[];
          }
        },
        CheckFun2(show){
          if(show==false){
            this.OrderContent.forEach((item,index)=>{
              if(item.Noselect==true){
                item.select=false;
              }
            });
          }else{
            this.OrderContent.forEach((item,index)=>{
              if(item.Noselect==true){
                item.select=true
              }
            });
          }
        },
        //跳转
        PcbParticulars(){
          this.$router.push({
            path:"OrderDetails"
          })
        },
        //头部组件调用当前此函数
        callMethod() {
          this.callMethodCut=!this.callMethodCut;
          this.OrderContent.forEach((item,index)=>{
            if(item.Noselect==true){
              item.select=true
            }
          });
        },
        //删除
        AddRemove(){
          this.callMethodCut=!this.callMethodCut;

        },
        //选中按钮
        SelectHe(data,index){
          data.select=!data.select;
          if(data.select==false){
            let c=this.CheckAllHome.find((err)=>{
              return err.name==data.name;
            });
            if(!c){
              this.CheckAllHome.push(data);
            }
          }
          console.log(this.CheckAllHome);
        }
      },
        created() {
          let objs={name:1,show:false,index:"2"};
          this.$emit('getHome', objs);
        }
    }

</script>

<style scoped>
  .ShoppingThinFlm .falseShow{width: 100%;height: 100%;padding-top: 1.5rem;text-align: center}
  .ShoppingThinFlm .falseShow img{width: 4.3rem;display: inline-block;margin-bottom: .3rem;}
  .ShoppingThinFlm .orderForm{margin-top: .24rem}
  .ShoppingThinFlm .orderForm .title{height: 1.02rem;line-height: 1.02rem;width: 100%;background: #fff;display: flex;justify-content: space-between;padding: 0 .3rem;border-bottom: 1px solid #E3E4E8;}
  .ShoppingThinFlm .orderForm .title p .icon_show{width: .4rem;height: .4rem;display: inline-block;vertical-align: middle;margin-right: .24rem}
  .ShoppingThinFlm .orderForm .title  p span{display: inline-block;vertical-align: middle;font-size: .34rem;color: #333}
  .ShoppingThinFlm .bg_icon{background: url("../../../assets/img/common/Nopitch.png") no-repeat;background-size: cover;}
  .ShoppingThinFlm .bg_icons{background: url("../../../assets/img/common/pitch.png") no-repeat; background-size: cover;}
  .ShoppingThinFlm .bg_gray{background: url("../../../assets/img/common/graypitch.png") no-repeat; background-size: cover;}
  .ShoppingThinFlm .orderForm .bottom{padding: .3rem;background: #fff;display: flex;justify-content: space-between;font-size: .28rem;color: #666}
  .ShoppingThinFlm .orderForm .bottom .left{width: .62rem;height: .62rem;vertical-align: middle}
  .ShoppingThinFlm .orderForm .bottom .left img{width: 100%}
  .ShoppingThinFlm .orderForm .bottom .right{width: 6rem;}
  .ShoppingThinFlm .orderForm .bottom .right div{margin-bottom: .15rem}
  .ShoppingThinFlm .orderForm .bottom .right .file{display: flex;justify-content: space-between;}
  .ShoppingThinFlm .orderForm .bottom .right .file .border{border: 1px solid #D1D3D8;width: .66rem;height: .40rem;line-height: .40rem;color: #666666;font-size: .24rem;text-align: center}
  .ShoppingThinFlm  .right .AddRemove{width: 1.2rem;height: .66rem;line-height: .66rem;border: 1px solid #f90;text-align: center;font-size: .28rem;color: #f90;    display: inline-block;vertical-align: middle}
  .fixationBottom {width: 100%;height: 1.12rem;line-height: 1.12rem;bottom: 0.9rem;position: fixed;background: #fff;display: flex;justify-content: space-between;padding: 0 .3rem;font-size: .28rem}
  .fixationBottom .right .buttom{width: 1.62rem;height: .66rem;line-height: .66rem;text-align: center;color: #fff;font-size: .28rem;display: inline-block}
  .fixationBottom .icon_show{width: .4rem;height: .4rem;display: inline-block;vertical-align: middle;}
</style>



